

  #container {
    margin: 10px;
    display: flex;
    flex-direction: column;
    background-color: lightblue;
  }

  .data {
    padding: 10px;
    background-color: lightblue;
    height: 100vh;
  }

  #blur {
   /* -webkit-filter: blur(3px);*/
  }

  #dogElements {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  

  #input {
    display: grid;
    margin: 20px;
    justify-content: center;
  }
  
  .dogImg {
    padding: 10px;
    max-width:100%;
    max-height:100%;
    object-fit: cover;
    border-radius: 15px;
  }

  .bigImg {
    position: absolute;
    background-color: black;
    z-index: 999;
  }
  
  #infos {
    display: flex;
    border: 1px solid black;
    flex-direction: row;
    margin: 2px;
    height: 100px;
    border-radius: 5px;
    justify-content: center;
    color: white;
    background-color: #282c34;
    border: 2px solid black;
  }

  #infoGrid {
    display: grid;
    grid-template-columns: repeat(2, 1fr)
  }

  #infos div:nth-child(1) {
    text-align: center;
    padding-top: 20px;
  }
  
  .dogInformation {
    display: flex;
    font-size: 15px;
    border: 1px solid black;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
  }
  
  .dogInformation div:nth-child(1){
    display: flex;
    flex-direction: row;
  }
  
  .dogInformation div:nth-child(1) div {
    padding-right: 10px;
  }
  
  .dogInformation div:nth-child(3) {
    display: flex;
    flex-direction: row;
  }
  
  .dogInformation div:nth-child(3) div {
    padding-right: 10px;
  }

  #searchTitle {
    text-align: center;
    color: white;
  }

  #searchDiv {
    background-color: #282c34;
    padding: 5px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 3px solid black;
  }


@media (max-width: 870px) {
  #dogElements {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 620px) {
  #dogElements {
    grid-template-columns: 1fr;
  }
}